<template>
    <div class="carousel-3d-controls">
        <a href="#" class="prev" @click.prevent="parent.goPrev()"
           :class="{ disabled: !parent.isPrevPossible }">
            <span>&lsaquo;</span>
        </a>
        <ul v-if="slidetitle.length>0" class="carousel-nav effect">
            <slide-controls v-for="i in parent.getSlideCount()" :currentIndex="parent.currentIndex" :index="i-1">
                <span @click.prevent="parent.goFar(i-1)">
                    {{slidetitle[i-1]}}
                </span>
            </slide-controls>
        </ul>
        <ul v-else class="carousel-nav">
            <li v-for="i in parent.getSlideCount()" @click.prevent="parent.goFar(i-1)"
            :class="parent.currentIndex+1==i?'active current':''">{{parent.currentIndex}}</li>
        </ul>
        <a href="#" class="next" @click.prevent="parent.goNext()"
           :class="{ disabled: !parent.isNextPossible }">
            <span>&rsaquo;</span>
        </a>
    </div>
</template>

<script>
    import SlideControls from './SlideControls'
    export default {
        components:{
          SlideControls
        },
        name: 'controls',
        props: {
            index: {type: Number },
            slidetitle: {type: Array, default: [] },
            // currentIndex:{type: Number,default: parent.currentIndex}
        },
        data () {
            return {
                parent: this.$parent,
                currentIndex: parent.currentIndex
            }
        },
    }
</script>

<style lang="css">
    .carousel-3d-controls {
        position: absolute;
        z-index: 9099;
        right:80px;
        display: table-cell;
        margin: -15px 0 0 0;
    }

    .next, .prev {
        display:block;
        width: 30px;
        z-index: 9999;
        font-size: 60px;
        height: 30px;
        line-height: 60px;
        color: #333;
        user-select: none;
        text-decoration: none;
        margin: auto;
    }

    .next span, .prev span {
        display:block;
        height:23px;
        width:23px;
        overflow:hidden;
        margin:0;
        border: solid #fff;
        border-width: 1px 0 0 1px;
        line-height: 140px;
        
    }
    .carousel-nav{
        margin:50px 0;
        /*height: 168px;*/
        max-height: 168px;
        overflow: hidden;
        overflow-y:auto;
        /*定义滚动条宽高及背景，宽高分别对应横竖滚动条的尺寸*/
    }
    .carousel-nav::-webkit-scrollbar{
        width: 5px;
        background-color: #f5f5f5;
    }
    /*定义滚动条的轨道，内阴影及圆角*/
    .carousel-nav::-webkit-scrollbar-track{
        box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: #000;
    }
    /*定义滑块，内阴影及圆角*/
    .carousel-nav::-webkit-scrollbar-thumb{
        height: 20px;
        border-radius: 5px;
        box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: #fff;
    }
    .carousel-nav li{
        width:6px;
        height:6px;
        background:#fff;
        overflow:hidden;
        border-radius:3px;
        margin: 4px auto;
        opacity:0.3;
        cursor: pointer;
    }
    .carousel-nav li.active{
        opacity:1;
    }

    .next:hover, .prev:hover {
        cursor: pointer;
        opacity: 0.7;
    }

    .prev span{
        transform:rotate(45deg) translateX(4px);
    }

    .next span{
        transform:rotate(-135deg) translateX(-4px);
    }

    .disabled {
        opacity: 0.2;
        cursor: default;
    }

    .disabled:hover {
        cursor: default;
        opacity: 0.2;
    }
</style>



