<template>
  <div class="memberhome">
        <layout-member :active="3">
          <slides v-if="database.length>0" :database="database" :slidetitle="[]"></slides>
        </layout-member>

        <div class="yearlist" v-if="category.length>0" >
          <h3><em>作品</em>分类</h3>
          <div class="menus">
            <div v-for="(item,idx) in categorytitle" @click="gotoSlider(idx)" :class="active == idx? 'active' : '' ">
            {{ item }}
            </div>
          </div>
        </div>
  </div>
</template>
<script>
  import Slides from '../modules/slider';
  import LayoutMember from '../modules/layout_member.vue'
  import {userdata} from '../mock-data/userdata.js'
  export default {
    components:{
      LayoutMember,
      Slides
    },
    data(){
      return{
        userdatas:userdata,
        database:[],
        category:[],
        categorytitle:[],
        active:0
      }
    },
    methods:{
      gotoSlider(idx){
        this.database = []
        this.active = idx
        setTimeout(()=>{
          this.database = this.category[idx];
        },0);
      }
    },
    created(){
      var name = this.$route.params.user;
      if(name&&this.userdatas&&this.userdatas[name]){
        var  dt = this.userdatas[name]['photo'];
        if(dt instanceof Array){
          this.database = this.userdatas[name]['photo'];
        }else{
          console.log("--->",dt)
          for(var a in dt){
            this.category.push(dt[a]);
            this.categorytitle.push(a);
          }
          this.database = this.category[0];
        }
      }
    }
  }
</script>

<style lang="less">

.yearlist{
  position: absolute;
  z-index: 9099;
  right: 70px;
  display: block;
  top :0;
  bottom :0;
  margin: auto;
  height: 281px;
  background: #000;
  /*定义滚动条宽高及背景，宽高分别对应横竖滚动条的尺寸*/
  .menus::-webkit-scrollbar{
    width: 5px;
    // height: 16px;
    background-color: #f5f5f5;
  }
  /*定义滚动条的轨道，内阴影及圆角*/
  .menus::-webkit-scrollbar-track{
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    // border-radius: 5px;
    background-color: #000;
  }
  /*定义滑块，内阴影及圆角*/
  .menus::-webkit-scrollbar-thumb{
    /*width: 10px;*/
    height: 20px;
    border-radius: 5px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #fff;
  }

  .menus{
    height: 228px;
    overflow: hidden;
    overflow-y:auto;
    cursor: pointer;
  }
  h3{
    color:#fff;
    font-size:14px;
    line-height:40px;
    em{
      border-bottom:2px solid #fff;
        display:inline-block;
    }
  }
  .active{
    color:#fff;
  }
  div{
    color:#666666;
    padding-top:15px;
    a{
      color:#676767;
    }
  }
  
} 
</style>



