<template>
  <div class="product">
    <div :class="showModal?'fullClose':''" @click="closeFullModal">
      <i class="icon-close"></i>
    </div>
    <div :class="showModal?'modal-mask':''">
      <div class="modal-wrapper">
        <carousel3d ref="carousel" :controls-visible="true" :style="fullstyle" v-on:SlidePoint="SlidePoint" :slidetitle="slidetitle" :border="borderWidth" :display="display">
          <slide v-for="(slide, i) in database" :index="i" :style="fullstyle"  >
            <img :class="database[i].video_url?'hide':''" :src="database[i].img" :alt="database[i].desc">
            <div class="video" v-if="showModal && database[i].video_url ">
              <d-player :video="video" :autoplay="autoplay" ref="player"> </d-player>
            </div>
            <div class="mask">
              <div class="bg"></div>
              <i class="fa fa-expand" @click="fullModal"></i>
            </div>
          </slide>
        </carousel3d>
      </div>
    </div>
  </div>
</template>
<script>
  import VueDPlayer from 'vue-dplayer'
  import { Carousel3d, Slide } from '../../slider/index';
  import LayoutMember from '../modules/layout_member.vue'
  import {userdata} from '../mock-data/userdata.js'
  export default {
    components:{
      LayoutMember,
      'carousel3d': Carousel3d,
      'slide': Slide,
      'd-player': VueDPlayer
    },
    data(){
      return{
        slides: [],
        showModal: false,
        display:this.displays||7,
        borderWidth:1,
        fullstyle:{
          // backgroundColor:"#000"
        },
        video: {
          url: '',
          pic: ''
        },
        autoplay: false
      }
    },
    props:['database','displays','slidetitle'],
    computed:{
      // fullstyle(){
      //   return this.
      // }
    },
    methods:{
      fullModal(){
        this.display = 1;
        this.showModal = true;
        this.borderWidth = 0;
        this.fullstyle['height'] = (document.body.clientHeight -100) + 'px';
        this.fullstyle['width'] = (document.body.clientWidth -100) + 'px !important';
        this.fullstyle['marginTop'] = 0 + 'px';

        var num = this.$refs.carousel.currentIndex;

        if(this.$refs.player&&this.$refs.player[num]&&this.$refs.player[num].dp&&this.$refs.player[num].dp.switchVideo){
          console.log("this.database[num].img::",this.database[num].img)
          this.$refs.player[num].dp.switchVideo({
            url: this.database[num].video_url,
            pic: this.database[num].img
          })
        }
        console.log("database[i].video_url::",this.database[num].video_url)
        if(num || num ===0){
          this.video.pic = this.database[num].img;
        }
      },
      SlidePoint(idx){
        if(this.$refs.player&&this.$refs.player[idx]&&this.$refs.player[idx].dp&&this.$refs.player[idx].dp.switchVideo&&this.database&&this.database[idx]&&this.database[idx].img){
          this.video.pic =this.database[idx].img;
          this.$refs.player[idx].dp.switchVideo({
            url: this.database[idx].video_url,
            pic: this.database[idx].img
          })
          this.video.pic = this.database[idx].img;
        }
        this.$emit('SlidePointProtfolio', idx);
      },
      closeFullModal(){
        this.display = 7;
        this.showModal = false;
        this.borderWidth = 1;
        this.fullstyle = {
          backgroundColor:"#000"
        };
      }
    }
  }
</script>

<style lang="less">
  .modal-wrapper{
    position: absolute;
    top: 0;
    bottom: 0;
    min-width: 870px;
    margin: auto;
    .carousel-3d-slider{
      width: 484px;
    }
    .carousel-3d-controls{
      right:190px !important;
    }
    .carousel-3d-container{
      padding-right: 190px !important;
      position: absolute !important;
      margin: auto;
      top: 0;
      bottom: 0;
      .carousel-nav.effect li{
          background-color: #000 !important;
      }
      .carousel-nav.effect{
        padding:23px 0;
        li{
          width: auto;
          height: auto;
          font-size: 12px;
          text-align: center;
          letter-spacing: 2px;
          padding-left: 2px;
        }
      }
    }
  }

  .product{
    color: #fff;
    width: 100%;
    height: 100%;
    .cur3 .mask .bg{background-color: rgba(0, 0, 0, 0.9); }
    .cur2 .mask .bg{background-color: rgba(0, 0, 0, 0.8); }
    .cur1 .mask .bg{background-color: rgba(0, 0, 0, 0.7); }
    .icon-close{
      display:none;
    }
    .fullClose{
      position:fixed;
      z-index:9999;
      top:40px;
      right:40px;
      width:32px;
      height:32px;
      .icon-close{
        display:block;
        z-index:9999;
        width:32px;
        height:32px;
        cursor:pointer;
        &:before{
          width:32px;
        }
        &:after{
          height:32px;
        }
      }
    }
    // 预览样式
    .modal-mask{
      position: fixed;
      z-index: 9998;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 1);
      display: table;
      transition: opacity .3s ease;
      .hide{display: none;}
      .video{
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        padding: 0 130px;
        iframe,.container,.__cov-video-container,#app{
          width: 100%;
          height: 100%;
        }
        .dplayer{
          height: 100%;
        }
      }
      .mask{
        display:none;
        background-color: #000;
      }
      .modal-wrapper {
        display: table-cell;
        vertical-align: middle;
        position: initial;
        .carousel-3d-container{
          padding-right: 0px !important;
          position: relative !important;
          margin: 20px auto;
          padding: 0;
          height: auto;
        }
      }
      .carousel-3d-controls{
        height: 60px;
        width: 100%;
        top: 45%;
        right:0 !important;
        margin-top:0;
      }
      .current{
        background-color: rgb(0, 0, 0) !important;
      }
      div.carousel-3d-slider{
        background:#000;
        width: inherit;
      }
      .prev,.next{
        position: absolute;
        height:60px;
        width:60px;
      }
      .prev{
        text-align: left;
        left: 50px;
      }
      .next{
        text-align: right;
        right: 50px;
      }
      .prev span,.next span{
        height:40px;
        width:40px;
        line-height:190px;
        color: #000;
        //transition: all 1s ease-in;
        transition: transform 800ms, opacity 800ms, visibility 800ms;

      }
      .prev span{
        transform: rotate(-45deg) translateX(4px);
      }
      .next span{
        transform: rotate(135deg) translateX(-4px);
        text-align: right;
      }
      .carousel-nav{
        display: none;
      }
    }
    .mask{
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      .bg,.fa-expand{
        transition: all .6s ease-in-out; 
      }
      .bg{
        position: absolute;
        background-color: rgba(0, 0, 0, 0.4);
        height: 100%;
        width: 100%;
        transition: all 1s ease-in-out;
      }
      .fa-expand{
        font-size: 33px;
        color:rgba(0, 0, 0, 0);
        top: 40%;
        position: relative;
      }
      &:hover{
        .bg{
          background-color: rgba(0, 0, 0, 0);
        }
        .fa-expand{
          color:rgba(255, 255, 255, 0.6);
          &:hover{
            color:rgba(255, 255, 255, 1);
            cursor: pointer;
          }
        }

      }
    }
  }

@media screen and (min-width: 320px) and (max-width: 768px) {
  .modal-wrapper{
      position:static;
  }

}
</style>




