<template>
    <li class="controls-slide" :class="[{ 'current': isCurrent },slideClass]" :indexsss="index">
        <slot></slot>
    </li>
</template>


<script>
    export default {
        name: 'slide-controls',
        props: {
            index: {
                type: Number
            },
            currentIndex:{
              type: Number
            }
        },
        data () {
            return {
                parent: this.$parent.parent,
                styles: {}
            }
        },
        computed:{
            isCurrent () {
                return this.index === this.parent.currentIndex;
            },
            slideClass() {
                // return 'cur';
                var name = 'cur';
                var num = this.index;
                if (!this.isCurrent){
                    const rIndex = this.getSideIndex(this.parent.rightIndices);
                    const lIndex = this.getSideIndex(this.parent.leftIndices);
                    if(rIndex>=0) name += rIndex+1;
                    if(lIndex>=0) name += lIndex+1;
                }
                return name;
            },
            slideStyle(idx) {
                let styles = {};
                if (!this.isCurrent) {
                    const rIndex = this.getSideIndex(this.parent.rightIndices);
                    const lIndex = this.getSideIndex(this.parent.leftIndices);

                    if (rIndex >= 0 || lIndex >= 0) {
                        styles = rIndex >= 0 
                            ? this.calculatePosition(this.index,this.parent.rightIndices,this.parent.leftIndices,rIndex, true) 
                            : this.calculatePosition(this.index,this.parent.rightIndices,this.parent.leftIndices,lIndex);
                        styles.opacity = 1;
                        styles.visibility = 'visible';
                    }

                    // console.log("rIndex",rIndex)
                    // console.log("lIndex",lIndex)
                    // console.log("styles",styles)
                    // console.log("this.parent.hasHiddenSlides",this.parent.hasHiddenSlides)

                    if (this.parent.hasHiddenSlides) {
                        if (this.matchIndex(this.parent.leftOutIndex)) {
                            styles = this.calculatePosition(this.index,this.parent.rightIndices,this.parent.leftIndices,this.parent.leftIndices.length - 1)
                        } else if (this.matchIndex(this.parent.rightOutIndex)) {
                            styles = this.calculatePosition(this.index,this.parent.rightIndices,this.parent.leftIndices,this.parent.rightIndices.length - 1, true)
                        }
                    }
                }
                console.log("styles:--->:",styles)

                return Object.assign(styles, {
                  // 'border-width': this.parent.border + 'px',
                  // 'border-color': this.parent.borderColor,
                  // 'width': this.parent.slideWidth + 'px',
                  // 'height': this.parent.slideHeight + 'px',
                  'transition': ' transform ' + this.parent.animationSpeed + 'ms, ' +
                  '               opacity ' + this.parent.animationSpeed + 'ms, ' +
                  '               visibility ' + this.parent.animationSpeed + 'ms'
                })
            }
        },
        methods:{
            getSideIndex (array) {
                let index = -1

                array.forEach((pos, i) => {
                    if (this.matchIndex(pos)) {
                        index = i
                    }
                })
                return index
            },
            matchIndex (index) {
                return (index >= 0) ? this.index === index : (this.parent.total + index) === this.index
            },
            calculatePosition (idx,rightIndices,leftIndices,i, positive) {
                const leftRemain = (this.parent.space === 'auto')
                    ? parseInt((i + 1) * (this.parent.width / 1.5), 10)
                    : parseInt((i + 1) * (this.parent.space), 10);
                    // console.log("-leftRemain::--->",leftRemain)
                // const transform = (positive)
                //     ? 'translateX(' + (leftRemain) + 'px) translateZ(-' + (this.parent.inverseScaling + ((i + 1) * 100)) + 'px) ' +
                //     'rotateY(-' + this.parent.perspective + 'deg)'
                //     : 'translateX(-' + (leftRemain) + 'px) translateZ(-' + (this.parent.inverseScaling + ((i + 1) * 100)) + 'px) ' +
                //     'rotateY(' + this.parent.perspective + 'deg)'
                // const top = this.parent.space === 'auto' ? 0 : parseInt((i + 1) * (this.parent.space))


                //console.log("==>",rightIndices,leftIndices,idx,idx-7)
                var translateY = (this.parent.inverseScaling + ((i + 1) * 40));
                var scale = 0.8

                if(rightIndices.indexOf(idx) ==2){
                    translateY = translateY+30
                    scale = 0.6
                }
                if(rightIndices.indexOf(idx) ==1){
                    translateY = translateY+20
                    scale = 0.7
                }
                if(rightIndices.indexOf(idx) ==0){
                    scale = 0.8
                }

                if(leftIndices.indexOf(idx) ==2){
                    translateY = translateY+10
                    scale = 0.6
                }
                if(leftIndices.indexOf(idx) ==1){
                    translateY = translateY+10
                    scale = 0.7
                }
                if(leftIndices.indexOf(idx) ==0){
                    scale = 0.8
                }

                const transform = (positive)
                    ? 'translateX(0px) translateY(' + translateY + 'px) ' + ' translateZ(-110px) '+
                    'rotateX(' + this.parent.perspective + 'deg) scale('+scale+')'
                    : 'translateX(0px) translateY(-' + translateY + 'px) ' + ' translateZ(-110px) '+
                    'rotateX(-' + this.parent.perspective + 'deg) scale('+scale+')';
                const top = 200;


                return {
                    transform: transform,
                    top: top,
                }

            },
        }
    }
</script>

<style scoped>
.controls-slide{
  opacity: 0;
  /*visibility: hidden;*/
  /*position: absolute;*/
  display: none;
  color: #484848;
}

.controls-slide.cur1,
.controls-slide.cur2,
.controls-slide.cur3
 {
  opacity: 1;
  /*visibility: visible;*/
  display: block;
}
.controls-slide.current.cur{
  color: #fff !important;
  opacity: 1;
  display: block;
}
</style>


